@import url(https://fonts.googleapis.com/css2?family=Noto+Serif&family=Playfair+Display&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Serif&family=Playfair+Display&display=swap);
body {
  margin: 0;
}


*{
  margin: 0;
  padding: 0;
}
.app
{
  background-color: #fafafa;
}

.content {
  background: #f4f9f9;
  margin-top: 15px;
  margin-left: 180px;
  width: 70%;
  height: 100%;
  /* border: 2px solid; */
}
.content_heading {
  padding: 10px 0 30px 5px;
  font-weight: 400;
  font-family: 'Playfair Display', serif;
}
.content_text {
  font-size: 22px;
  font-family: 'Noto Serif', serif;
  text-align: left;
  padding: 10px 0 30px 5px;
}
.content_subtopics {
  font-family: 'Noto Serif', serif;
  padding: 10px 0 30px 5px;
}
table,
th,
td {
  font-family: 'Noto Serif', serif;
  border: 1px solid black;
  padding: 3px 35px;
}
img
{
  width: 100%;
  height: 9rem;
}
li
{
  font-size: 25px;
}
@media screen and (max-width: 990px)
{
  .content
  {
    margin-left: 10px;
    width: 100%;
  }
}
.sidenav {
  width: 150px;
  height: 100%;
  position: fixed;
  z-index: 1;
  top: 70px;
  /* left: 10px; */
  background: #fff;;
  overflow-x: hidden;
  padding: 21px 0;
}

.sidenav a {
  padding: 16px 8px 6px 16px;
  text-decoration: none;
  font-size: 25px;
  color: rgb(138, 100, 31);
  display: block;
  border-bottom: 0.1px solid;
}

.sidenav a:hover {
  color: #064579;
}

.main {
  margin-left: 140px; /* Same width as the sidebar + left position in px */
  font-size: 28px; /* Increased text to enable scrolling */
  padding: 0px 10px;
}

@media screen and (max-width: 990px) {
  .sidenav {
    position:fixed;
    margin-top:  12.5rem;
    padding-top: 15px;
    width: 00px;
  }
  .sidenav a {
    font-size: 18px;
  }
}
@media screen and (max-height: 900px) {
  .sidenav {
    position:fixed;
    height: 50%;
  }
  .sidenav a {
    font-size: 18px;
  }
}
.footer
{
    color: white;
    text-align: center;
    background-color: #343a40;
    margin-top: 20px;
    width: 100%px;
    height: 140px;
   font-family: 'Noto Serif', serif;
}
.footer>h1
{
    font-weight: 100;
}


